html{
    font-size: 10px;
}


html,
body{
   
    margin: 0;
    padding:0;
}
html,
body,
#root,
.App{
    height: 100%;
}

*,::before, ::after{
    --webkit-box-sizing: border-box;
    --moz-box-sizing: border-box;
    box-sizing: border-box;

}
body{
    font-family: 'Montserrat', sans-serif;
    font-size:1.8rem;
    line-height: 1;
    font-weight: 400;

}

a,a:hover{
  --webkit-transition: all .4s ease-in-out;
  --moz-transition: all .4s ease-in-out;
  transition: all .4s ease-in-out;

}

.App{
    width: 100%;
    max-width:1450px;
    padding: 0 10px;
    margin: 0 auto;
}

.main{
    width: 100%;
    height:calc(100% - 6.5rem);
    max-width: 1450px;
    padding: 0 ;
    margin: 0 auto;
}

.title{

    font-size: 30px;
    padding-top: 20px;
    text-align: center;
}

ul{
 
   display: flex;
   float:right;
   position: absolute;
   margin-top: 0;
   margin-bottom: 2px;;

   
 
    
}
ul li{
    
    color:white;
   
    font-size:24px;
    
}

ul li a {
    width: 60px;
    padding: 16px;
    color:white;
    display:block;
}

.fab fa-facebook{
    color:yellow;
    background-color: yellow;
    }
    